.main {
  display: flex;
  flex-direction: column;
  padding: 20px;
  max-width: 40rem;
}

.item1,
.item2,
.item3,
.item4 {
  margin-bottom: 20px;
  opacity: 0;
}

.item2,
.item3,
.item4 {
  font-size: 18px;
  text-align: justify;
  font-weight: 700;
}

.item1 {
  margin-bottom: 20px;
  transform: translate(0, -10px);
  opacity: 0;
  transition: opacity 1s, transform 1s ease;
}

.item2 {
  transform: translate(0, -15px);
  transition: opacity 1.5s, transform 1.5s ease;
}

.item3 {
  transform: translate(0px, -20px);
  transition: opacity 2s, transform 2s ease;
}

.item4 {
  transform: translate(0px, -25px);
  transition: opacity 2.5s, transform 2.5s ease;
}

.loaded {
  opacity: 1;
  transform: translate(0, 0);
}

@media only screen and (max-width: 500px) {
  .item2,
  .item3,
  .item4 {
    text-align: center;
  }
}
